
"use strict";

class ProductCategoryRow extends React.Component{
    render(){
        const category = this.props.category;
        return (
            <tr>
                <th colSpan = "2">
                    {category}
                </th>
            </tr>
        );
    }
}

class ProductRow extends React.Component{
    render(){
        const product = this.props.product;
        const name = product.stocked ? 
            product.name : 
                <span style = {{color: 'red'}}>
                    {product.name}
                </span>;
        return (
            <tr>
                <td>{name}</td>
                <td>{product.price}</td>
            </tr>
        );   
    }
}

class ProductTable extends React.Component{
    render(){
        const rows = [];
        let lastCategory = null;

        this.props.products.forEach(product => {
            if(product.category !== lastCategory){
                rows.push(
                    <ProductCategoryRow 
                        category = {product.category}
                        key = {product.category}
                     />
                );
                lastCategory = product.category;
            }
            rows.push(
                <ProductRow 
                    product = {product}
                    key = {product.name}
                />
            );
        });
        return(
            <table>
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Price</th>
                    </tr>
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </table>
        );
    }
}

class SearchBar extends React.Component {
    render(){
        return(
            <form>
                <input type='text' placeholder="Search..." onKeyUp = {this.props.onFilterTestChange}/>
                <p>
                    <input type="checkbox" onClick = {this.props.onStockedChange}/>
                    {' '}
                    Only show products in stock
                </p>
            </form>
        );
    }
}

class FilterableProductTable extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isStockedOnly: false,
            filterText: '',
        }
        this.onFilterTestChange = this.onFilterTestChange.bind(this);
        this.onStockedChange =this.onStockedChange.bind(this);

    }
    onFilterTestChange(e){
        this.setState({filterText:e.target.value});
    }
    onStockedChange(){
        this.setState((state)=>({isStockedOnly:!state.isStockedOnly}))
    }
    render(){
        return(
            <div>
                <SearchBar  
                    onFilterTestChange = {this.onFilterTestChange}
                    onStockedChange = {this.onStockedChange}
                />
                <ProductTable 
                    products = {this.filter(this.props.products)} 
                />
            </div>
        );
    }
    filter(products){
        if(this.state.filterText !== ''){
            return products.filter((product)=>product.name.includes(this.state.filterText)
            &&(product.stocked||(!this.state.isStockedOnly)));
        }else{
            return products.filter((product)=>product.stocked||(!this.state.isStockedOnly));
        }
        
    }
}

const PRODUCTS = [
    {category:'Sporting Goods',price: '$49.99',stocked: true,name:'Football'},
    {category:'Sporting Goods',price: '$9.99',stocked: true,name:'Baseball'},
    {category:'Sporting Goods',price: '$29.99',stocked: false,name:'Basketball'},
    {category:'Electronics',price: '$99.99',stocked: true,name:'iPod Touch'},
    {category:'Electronics',price: '$399.99',stocked: false,name:'iPhone 5'},
    {category:'Electronics',price: '$199.99',stocked: true,name:'Nexus 7'},
];

ReactDOM.render(<FilterableProductTable products = {PRODUCTS}/>, document.getElementById('container'));